import React from 'react';
import { useDispatch } from 'react-redux';
import Swal from 'sweetalert2';
import { startLogin, startRegister } from '../../actions/Auth';
import { useForm } from '../../hooks/useForm';
import './Login.css';

export const LoginRegisterScreen = () => {

    const dispatch =  useDispatch()

    const [handlelogin, handleLoginChange ] = useForm({
         lEmail: '',
         lPassword: ""
    })

    const {lEmail, lPassword} = handlelogin

    const handleLoginForm = (e) =>{
       e.preventDefault()
       //console.log(handlelogin)
 
       dispatch(startLogin(lEmail, lPassword))
 
    }
    const [handleRegister, handleRegisterChange ] = useForm({
        rname:'',
        rEmail: '',
        rPassword: "",
        rpassword2: ''

   })

   const {rname, rEmail, rPassword, rpassword2} = handleRegister

   const handleRegisterForm = (e) =>{
    e.preventDefault()
    //console.log(handlelogin)

    if(rPassword !== rpassword2){
       return Swal.fire('Error', 'Las dos contraseñas deben ser iguales', 'error')
    }

    dispatch(startRegister(rname,rEmail, rPassword ))

 }


    return (
        <div className="container login-container">
            <div className="row">
                <div className="col-md-6 login-form-1">
                    <h3>Ingreso</h3>
                    <form onSubmit={handleLoginForm}>
                        <div className="form-group mb-2">
                            <input 
                                type="text"
                                className="form-control"
                                placeholder="Correo"
                                name='lEmail'
                                value={lEmail}
                                onChange={handleLoginChange}
                            />
                        </div>
                        <div className="form-group mb-2">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contraseña"
                                name='lPassword'
                                value={lPassword}
                                onChange={handleLoginChange}
                            />
                        </div>
                        <div className="form-group mb-2">
                            <input 
                                type="submit"
                                className="btnSubmit"
                                value="Login" 
                            />
                        </div>
                    </form>
                </div>

                <div className="col-md-6 login-form-2">
                    <h3>Registro</h3>
                    <form onSubmit={handleRegisterForm}>
                        <div className="form-group mb-2">
                            <input
                                type="text"
                                className="form-control"
                                placeholder="Nombre"
                                name='rname'
                                value={rname}
                                onChange={handleRegisterChange}
                            />
                        </div>
                        <div className="form-group mb-2">
                            <input
                                type="email"
                                className="form-control"
                                placeholder="Correo"
                                name='rEmail'
                                value={rEmail}
                                onChange={handleRegisterChange}
                            />
                        </div>
                        <div className="form-group mb-2">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contraseña"
                                name='rPassword'
                                value={rPassword}
                                onChange={handleRegisterChange} 
                            />
                        </div>

                        <div className="form-group mb-2">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Repita la contraseña"
                                name='rpassword2'
                                value={rpassword2}
                                onChange={handleRegisterChange} 
                            />
                        </div>

                        <div className="form-group mb-2">
                            <input 
                                type="submit" 
                                className="btnSubmit" 
                                value="Crear cuenta" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}