import { types } from "../types/types"



const initialstate = {
  cheking: true,
  //uid: null,
  //name: null


}

 export const authReducer = (state = initialstate, action) =>{
 switch (action.type) {
    case types.authLogin:

    return {
      ...state,
      cheking: false,
      ...action.payload
    }
        
     case types.authChekungfinish:
      return {
        ...state,
        cheking: false
      } 
      
      case types.authlogout:
        return{
          
          cheking: false
        }
 
    default:
        return state
 }

}