import React, { useState } from 'react'
import { NavBar } from '../Ui/NavBar'
import { Calendar, momentLocalizer } from 'react-big-calendar'
import moment from 'moment'
import 'react-big-calendar/lib/css/react-big-calendar.css'
import 'moment/locale/es'
import { messages } from '../../helpers/calendar-messages-es'
import { CalendarEvent } from './CalendarEvent'
import { CalendarModel } from './CalendarModel'
import { useDispatch, useSelector } from 'react-redux'
import { uiOpenModal } from '../../actions/Ui'
import { CalendarActiveNote, limpiarEventoActivo, startLoadEvents } from '../../actions/Calendar'
import { AddNewFab } from '../Ui/AddNewFab'
import { AddDeleteFab } from '../Ui/AddDeleteFab'
import { startCheking } from '../../actions/Auth'
import { useEffect } from 'react'
moment.locale('es')
const localizer = momentLocalizer(moment)



export const CalendarScreen = () => {
  const dispatch =  useDispatch()
  const {event, ActiveEvent} = useSelector( state => state.calendar) 
  const {uid} = useSelector( state => state.Auth) 
  const [ViuLast, setViuLast] = useState(localStorage.getItem('lastViu') || 'month')
 const dobleClick = (e) =>{
   dispatch(uiOpenModal())    
 }

 useEffect(() => {
     dispatch(startLoadEvents())
 }, [dispatch, ])
 

 const SelectEvento = (e) =>{
    //console.log(e)

    dispatch(CalendarActiveNote(e))
 }

 const onViuChange = (e) =>{
    setViuLast(e)

    localStorage.setItem('lastViu', e)

 }

 const noteActiveNull = () =>{
    dispatch(limpiarEventoActivo())
 }

 


 const estiloEvento = (event, start,end, isSelected) =>{

  
   const style = {
    backgroundColor: ( uid === event.user._id  ) ? '#367cf7' : '#465660',
    borderRadius: '0px',
    opacity: 0.8,
    display: 'block',
    color: 'white',
    
   }  
  return{
    style
  }

 }

  return (
    <div className='calendar-screen'>
        <NavBar/>

        <Calendar
          localizer={localizer}
          events={event}
          startAccessor="start"
          endAccessor="end"
          messages={messages}
          eventPropGetter={estiloEvento}
          onDoubleClickEvent={dobleClick}
          onSelectEvent={SelectEvento}
          onSelectSlot={noteActiveNull}
          selectable= {true}
          view={ViuLast}
          onView={onViuChange}
          components={{
            event: CalendarEvent 
          }}
        />
        <AddNewFab/>

        
        {
          (ActiveEvent) && 
          <AddDeleteFab/>
        }
          
        
        

        <CalendarModel/>
    </div>
  )
}
