import Swal from "sweetalert2";
import { fetchConToken } from "../helpers/Fetch"
import { prepararEvents } from "../helpers/prepararEvents";
import { types } from "../types/types"

export const NewNote = (Event) =>{
    return  async (dispatch, getState) =>{

         const {uid, name} = getState().Auth;
         const guardardb = await fetchConToken('journal/Newnote', Event, 'POST' )
         const body = await guardardb.json()
         

         try {
           if(body.ok){
             Event.id = body.evento.id;
             Event.user = {
                _id: uid,
                name,
             }
             dispatch(CalendarNewNote(Event))
           } 
         } catch (error) {
            console.log(error)
         }

    }
}
 const CalendarNewNote = (event) =>({
    
        type: types.calendarNewNote,
        payload: event
    
})

export const CalendarActiveNote = (event) =>{
    
    return{
        type: types.calendarActiveNote,
        payload: event
    }
}

export const limpiarEventoActivo = () =>{

   return{
    type: types.calendarClearEventActive,
   } 
}

export const startUpdateNote = (evento)=>{

    
    return async( dispatch, getState) =>{
        const {ActiveEvent} = getState().calendar;
        
        const endpoint = `journal/${ActiveEvent.id}`
        const resp = await fetchConToken(endpoint, evento, 'PUT')
        const body = await resp.json()

        //console.log(body.eventos)
  try {
    if(body.ok){
        dispatch(UpdateNote(evento))

    }else{
        Swal.fire('Error', body.msg, 'error')
    }
    
  } catch (error) {
    console.log(error)
  }


    }
}


 const UpdateNote = (event) =>{
    return{
        type: types.calendarUpdateNote,
        payload: event

    }
}

export const startDeleteNote = ()=>{

    
    return async( dispatch, getState) =>{
        const {ActiveEvent} = getState().calendar;
        
        const endpoint = `journal/${ActiveEvent.id}`
        const resp = await fetchConToken(endpoint, {}, 'DELETE')
        const body = await resp.json()

        //console.log(body.eventos)
  try {
    if(body.ok){
        dispatch(DeleteNote())

    }else{
        Swal.fire('Error', body.msg, 'error')
    }
    
  } catch (error) {
    console.log(error)
  }


    }
}

 const DeleteNote = () =>({type: types.calendarDeleteNote})


export const startLoadEvents = () =>{
    return async (dispatch) =>{
        const resp = await fetchConToken('journal')
        const body = await resp.json()
         const eventos = prepararEvents(body.msg) 

         dispatch(loadnotes(eventos))
        
    }
}


const loadnotes = (eventos) =>({
  type: types.calendarLoadEvets,
  payload: eventos
})

export const calendarLogout = () =>({type: types.calendarLogout})