import Swal from "sweetalert2";
import { fetchConToken, fetchSinToken } from "../helpers/Fetch"
import { types } from "../types/types";
import  {calendarLogout} from './Calendar'

export const startLogin =  (Email, password) => {

     return  async (dispatch) =>{
          const peticion = await fetchSinToken('auth', {Email, password}, 'POST');
          const body = await peticion.json()

         

           if(body.ok){
             localStorage.setItem('token', body.token)
             localStorage.setItem('token-init-date', new Date().getTime());
            
             dispatch(login({
                uid: body.uid,
                name: body.name
             }))


          }else{
            Swal.fire('Error', body.msg, 'error')
          } 

     }
  

   }
   export const startRegister =  ( name,Email, password) => {
   
      return  async (dispatch) =>{
           const peticion = await fetchSinToken('auth/New', {name ,Email, password}, 'POST');
           const body = await peticion.json()
   
          
   
            if(body.ok){
              localStorage.setItem('token', body.token)
              localStorage.setItem('token-init-date', new Date().getTime());
             
              dispatch(login({
                 uid: body.uid,
                 name: body.name
              }))
   
   
           }else{
             Swal.fire('Error', body.msg, 'error')
           } 
   
      }
   
   
   }

export const login = (user) =>({
    type: types.authLogin,
    payload: user

})


export const startCheking = ()=>{
   return async(dispatch) =>{
      const respuesta = await fetchConToken('auth/Newtoken')
      const body = await respuesta.json();

      

      if(body.ok){
         localStorage.setItem('token', body.token)
         localStorage.setItem('token-init-date', new Date().getTime());
        
         dispatch(login({
            uid: body.uid,
            name: body.name
         }))


      }else{
        
        dispatch(chekingFinish());

      } 


   }
}

export const chekingFinish = () =>({
   type: types.authChekungfinish,
   

})

export const startLogOut = () =>{
   return (dispatch) =>{
       localStorage.clear()
       dispatch(loginOut())
       dispatch(calendarLogout())
   }
}

export const loginOut = () =>({
  type: types.authlogout
})