const initialState = {

    event:[],

    ActiveEvent: null

}

export const calendarReducers = (state = initialState, action) =>{
 switch (action.type) {
    case types.calendarActiveNote:
        return{
            ...state,
            ActiveEvent: action.payload
        }

     case types.calendarNewNote:
        return{
            ...state,
            event:[
                ...state.event,
                action.payload
            ]
        }   

        case types.calendarClearEventActive:
            return{
                ...state,
                ActiveEvent: null
            }

        case types.calendarUpdateNote:
            return{
                ...state,
                event: state.event.map(e =>(e.id === action.payload.id)? action.payload : e )
            }   
            
            case types.calendarDeleteNote:
            return{
                ...state,
                event: state.event.filter(e =>(e.id !== state.ActiveEvent.id)),

                ActiveEvent: null
            }  

            case types.calendarLoadEvets:
              return{
                  ...state,
                 event: [...action.payload]
              }

              case types.calendarLogout:
                return{
                    ...initialState
                }

        
 
    default:
        return state;
 }
}