import React, { useEffect, useState } from 'react'
import Modal from 'react-modal';
import DateTimePicker from 'react-datetime-picker';
import moment from 'moment';
import 'react-datetime-picker/dist/DateTimePicker.css';
import 'react-calendar/dist/Calendar.css';
import 'react-clock/dist/Clock.css';
import Swal from 'sweetalert2';
import { useDispatch, useSelector } from 'react-redux';
import { uiClosModal } from '../../actions/Ui';
import {  limpiarEventoActivo, NewNote, startUpdateNote } from '../../actions/Calendar';

const tiempoInicial = moment().seconds(0).minutes(0).add(1, 'hours');
const tiempoFinal = tiempoInicial.clone().add(1, 'hours');
const initialDatesModal = {
        title: '',
        note:'',
        start: tiempoInicial.toDate(),
        end: tiempoFinal.toDate()
}

export const CalendarModel = () => {

  const {openModal} = useSelector(state => state.ui) 
  const {ActiveEvent} = useSelector(state => state.calendar) 
  //const {name, uid} = useSelector(state => state.Auth) 
  

  const dispatch = useDispatch()   
  const customStyles = {
      content: {
           top: '50%',
           left: '50%',
          right: 'auto',
          bottom: 'auto',
          marginRight: '-50%',
          transform: 'translate(-50%, -50%)',
        },
    };
    
    Modal.setAppElement('#root');
    const [dateTimeStar, setdateTimeStart] = useState(tiempoInicial.toDate())
    const [dateTimeEnd, setdateTimeEnd] = useState(tiempoFinal.toDate()) 
    
    const [tituloValido, settituloValido] = useState(true)

    const [FormValue, setFormValue] = useState(initialDatesModal)
    
    const {title, note, start, end} = FormValue

    useEffect(() => {
       if(ActiveEvent){
         setFormValue(ActiveEvent)
       }else{
         setFormValue(initialDatesModal)
       }
    }, [ActiveEvent, setFormValue])
    
    
    const closeModal = () =>{
       dispatch(uiClosModal()) 
       dispatch(limpiarEventoActivo())
       setFormValue(initialDatesModal)
    }
    const handleInputChange = ({target}) =>{
          
        setFormValue({
            ...FormValue,
            [target.name]: target.value
        })
    }
    
    const handleStartDateChange = (e) =>{
        setdateTimeStart(e)

        setFormValue({
            ...FormValue,
            start: e
        })
        
        
    } 

    const handleEndDateChange = (e) =>{
       setdateTimeEnd(e)

       setFormValue({
        ...FormValue,
        end: e
    })
    }
  const handleSubmitForm = (e) =>{
      e.preventDefault()

      const fechaStart = moment(start)
      const fechaEnd = moment(end)

      if(fechaStart.isSameOrAfter(fechaEnd)){
        return Swal.fire('Error', 'La fecha final debe ser mayor que la fecha de inicio', 'error');
      }
      if(title.trim().length < 2){
        return  settituloValido(false) ;
      }

      settituloValido(true)

      if(ActiveEvent){
         dispatch(startUpdateNote(FormValue))
      }else{
          dispatch(NewNote(FormValue))

      }


      closeModal();
  }

  return (
    <div>

<Modal
        isOpen={openModal}
        //onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        closeTimeoutMS={200}
        className= ' modal'
        overlayClassName='modal-fondo'
      >
      
      <h1> { (ActiveEvent)?'Editar Evento' : 'Nuevo evento'} </h1>
<hr />
<form 
className="container"
onSubmit={handleSubmitForm}
>

    <div className="form-group mb-2">
        <label>Fecha y hora inicio</label>
        <DateTimePicker
         onChange={handleStartDateChange}
         value={dateTimeStar}
         className= "form-control  " 
         format='y-MM-dd h:mm:ss a'
        />
    </div>

    <div className="form-group mb-2">
        <label>Fecha y hora fin</label>
        <DateTimePicker
         onChange={handleEndDateChange}
         value={dateTimeEnd}
         className= "form-control" 
         format='y-MM-dd h:mm:ss a'
         minDate={dateTimeStar}
        />
    </div>

    <hr />
    <div className="form-group mb-2">
        <label>Titulo y notas</label>
        <input 
            type="text" 
            className={`form-control ${ !tituloValido && 'is-invalid'}`}
            placeholder="Título del evento"
            name="title"
            autoComplete="off"
            value={title}
            onChange={handleInputChange}
        />
        <small id="emailHelp" className="form-text text-muted">Una descripción corta</small>
    </div>

    <div className="form-group mb-2">
        <textarea 
            type="text" 
            className="form-control"
            placeholder="Notas"
            rows="5"
            name="note"
            value={note}
            onChange={handleInputChange}
        ></textarea>
        <small id="emailHelp" className="form-text text-muted">Información adicional</small>
    </div>

    <button
        type="submit"
        className="btn btn-outline-primary btn-block"
    >
        <i className="far fa-save"></i>
        <span> Guardar</span>
    </button>

</form>
</Modal>
    </div>
  )
}
